/*eslint-disable*/module.exports={messages:{"### Ihre Rechte bei Annullierung Ihres Flugs nach der EU-Fluggastrechteverordnung\nWenn Ihr Flug durch die Fluglinie annuliert wurde, haben Sie vielleicht einen Anspruch auf einen alternativen Flug oder R√ºckerstattung der Flugscheinkosten. Bei einer kurzfristigen Annulierung k√∂nnen Sie au√üerdem eine Ausgleichszahlung erhalten, wenn Ihnen kein vergleichbarer Flug angeboten wurde. Zus√§tzlich k√∂nnen Anspr√ºche auf Betreuungsleistungen wie Essen und Getr√§nke bei einem versp√§teten Start entstehen.":"### Your rights when your flight is cancelled under the EU Passenger Rights Regulation\nIf your flight was cancelled by the airline, you may be eligible for an alternative flight or a refund of ticket costs. With a short-term cancellation, you can also receive a chargeback if you haven't been offered a comparable flight. In addition, claims to care services such as food and drinks may arise in the event of a late start.","### Ihre Rechte bei Bef√∂rderungsverweigerung nach der EU-Fluggastrechteverordnung\nWenn Ihnen die Mitreise verweigert wurde, weil Ihr Flug √ºberbucht war, und Sie nicht freiwillig ein alternatives Angebot Ihrer Flugline angenommen haben, haben Sie vielleicht einen Anspruch auf eine Ausgleichszahlung. Zus√§tzlich k√∂nnen Anspr√ºche auf Betreuungsleistungen wie Essen und Getr√§nke entstehen.":"### Your rights in case of denial of carriage under the EU Passenger Rights Regulation\nIf you were denied travel because your flight was overbooked and you did not voluntarily accept an alternative offer from your airline, you may be entitled to a chargeback. In addition, claims to care services such as food and drinks may arise.","### Ihre Rechte bei Versp√§tung nach der EU-Fluggastrechteverordnung\nWenn Ihre Landung am Zielflughafen mehr als drei Stunden versp√§tet war, haben Sie vielleicht einen Anspruch auf eine Ausgleichszahlung. Zus√§tzlich k√∂nnen schon ab zwei Stunden Versp√§tung beim Start Anspr√ºche auf Betreuungsleistungen wie Essen und Getr√§nke entstehen.":"### Your rights in case of delay under the EU Passenger Rights Regulation\nIf your landing at your destination airport was more than three hours late, you may be eligible for a chargeback. In addition, a delay of just two hours at the start may incur entitlements to support services such as food and drinks.","### Ihre Rechte nach der EU-Fluggastrechteverordnung\nWenn Sie wegen einer Flugversp√§tung, einer Annullierung durch die Fluglinie oder einer Bef√∂rderungsverweigerung wegen √úberbuchung Ihr Ziel versp√§tet oder gar nicht erreichen, haben Sie nach der EU-Fluggastrechteverordnung vielleicht einen Anspruch auf Ausgleichszahlung und Betreuungsleistungen. Die H√∂he der  Ausgleichszahlung ist abh√§ngig von der Entfernung des Reiseziels und kann bis zu 600 Euro betragen. Zus√§tzlich k√∂nnen Sie bei Annullierung einen alternativen Flug w√§hlen, oder sich Ihre Flugscheinkosten erstatten lassen. \nHier helfen wir Ihnen, m√∂gliche Anspr√ºche f√ºr Ihren Fall durchzusetzen.":"### Your rights under the EU Passenger Rights Regulation\nIf you are delayed or unable to reach your destination due to a flight delay, airline cancellation, or overbooking denied carriage, you may be entitled to compensation and assistance under the EU Passenger Rights Regulation. The amount of the compensation depends on the distance from the destination and can amount to up to 600 euros. In addition, if you cancel, you can choose an alternative flight or have your ticket costs reimbursed. \nHere, we help you enforce possible claims for your case.","### Was ist ein **Mietmangel**?\nIhre Vermieter:in muss Ihnen Ihre Wohnung in einem geeigneten Zustand √ºberlassen und erhalten. Ist die **Wohnung in einem erheblich schlechteren Zustand** als erwartet werden kann, liegt vielleicht ein Mangel vor. Dann k√∂nnen Sie verschiedene Anspr√ºche haben, beispielsweise auf Beseitigung des Mangels oder Mietminderung.":"### What is a **rent deficiency**?\nYour landlord:in must leave and maintain your apartment in a suitable condition. If the **apartment is in a significantly worse condition** than can be expected, there may be a defect. Then you can have various claims, for example to remedy the defect or to reduce rent.","### Wohnungsm√§ngel durch L√§rm\nErheblicher L√§rm kann als Mietmangel gelten, beispielswese bei St√∂rungen in den Ruhezeiten. Diese sind in der Regel zwischen 22 und 6 Uhr, sowie ganzt√§gig an Sonn- und Feiertagen. Andere Ruhezeiten k√∂nnen von Bundesl√§ndern und Gemeinden, aber auch in der Hausordnung festgehalten werden.\n‚úÖ **Unter Ruhest√∂rungen fallen**¬†St√∂rungen der Nachtruhe (Musik, Staubsaugen, laute Waschmaschine √ºber 30 Dezibel), dauerhafte Ger√§usche von Tieren, laute Gartenger√§te, sowie Streitgespr√§che.\n‚ùå **Nicht als Ruhest√∂rung z√§hlen** n√§chtliches Duschen, Renovierungs- und Baul√§rm, spielende Kinder, oder Kirchenglocken.\nüí° **Hinweis bei Nachbarschaftskonflikten:** Sollten Sie sich von einer Nachbar:in gest√∂rt f√ºhlen, sollten Sie zun√§chst freunddlich darauf ansprechen, um Streit zu vermeiden. M√∂glicherweise ist der Nachbar:in nicht klar, dass Sie sich gest√∂rt f√ºhlen. Bei akuten, sehr starken St√∂rungen k√∂nnen Sie auch das Ordnungsamt oder die Polizei kontaktieren.":"### Housing deficiencies due to noise\nSignificant noise can be considered a lack of rent, for example in case of disruptions during rest periods. These are usually between 10pm and 6am, as well as all day on Sundays and public holidays. Other rest periods can be recorded by federal states and municipalities, as well as in the house rules.\n‚úÖ **Disturbed peace** Disturbances of sleep (music, vacuuming, noisy washing machine over 30 decibels), persistent sounds from animals, loud garden tools, and arguments.\n‚ùå **Don't count as a disturbance of peace** Nightly showering, renovation and construction noise, children playing, or church bells.\nüí° **Neighborhood conflict note**: If you feel disturbed by a neighbor, you should first approach it kindly to avoid quarrels. The neighbor may not realise that you are feeling disturbed. In case of acute, very severe disturbances, you can also contact the public order office or the police.","### Wohnungsm√§ngel durch Schimmel\nSchimmelpilze sehen aus wie schwarze Flecken und entstehen bei zu hoher Feuchtigkeit in der Wohnung, beispielsweise durch seltenes L√ºften, sparsames Heizen oder Baum√§ngel. Schimmel kann sich sowohl negativ auf die Gesundheit, als auch die W√§nde der Wohnung auswirken. [Mehr k√∂nnen Sie auf der Webseite der Verbraucherzentrale erfahren.](https://www.verbraucherzentrale.de/wissen/umwelt-haushalt/wohnen/schimmel-in-der-wohnung-6794#)":"### Housing defects due to mold\nMolds look like black spots and are formed when humidity is too high in the home, for example due to infrequent ventilation, economical heating or construction defects. Mold can have a negative impact on both health and the walls of the apartment. [You can find out more on the website of the consumer center.](https://www.verbraucherzentrale.de/wissen/umwelt-haushalt/wohnen/schimmel-in-der-wohnung-6794#)","### Wohnungsm√§ngel durch Wassersch√§den\nWassersch√§den in der Wohnung k√∂nnen beispielsweise durch starken Regen oder Sch√§den an Wasserleitungen, Ger√§ten und im Bad entstehen.\nAnspr√ºche gegen Vermieter:innen oder Hauseigent√ºmer:innen wegen des Wasserschadens bestehen dann, wenn diese den Mangel verschuldet haben, was beispielsweise oft bei Rohrbr√ºchen der Fall ist.\nWerden Wassersch√§den durch eigene defekte Ger√§te wie Waschmaschine oder Geschirrsp√ºler verursacht, haftet man in der Regel selbst. Etwas anderes kann gelten, wenn die Ger√§te mit der Wohnung angemietet wurden (bspw. Ger√§te in einer angemieteten Einbauk√ºche).\nWenn die Mietenden den Wasserschaden selbst verursacht haben, dann kann eine Hausratsversicherung oder bei Sch√§den der Nachbarn eine Haftpflichtversicherung weiterhelfen.":"### Housing deficiencies due to water damage\nWater damage in the home can be caused, for example, by heavy rain or damage to water pipes, appliances and in the bathroom.\nThere are claims against landlords or homeowners for water damage when they have caused the defect, which is often the case with pipe bursts, for example.\nIf water damage is caused by defective appliances such as washing machines or dishwashers, you are usually liable yourself. Something different may apply if the equipment was rented with the apartment (e.g. appliances in a rented fitted kitchen).\nIf the tenants have caused the water damage themselves, household insurance or liability insurance can help in case of damage to neighbours.","- **Anspruch**\n- Vermieter:innen haben die Pflicht, die Mietsache in einem Zustand\nzu √ºbergeben und zu erhalten, die der vertraglichen Vereinbarung entspricht. \n- Daf√ºr zahlen die Mietenden die Miete. \n- Wenn w√§hrend der Mietzeit ein Mangel auftritt, dann muss die Vermieter:in ihn beseitigen. \n- **Ausnahme**: bei Kleinreparaturen bis zu einem Betrag von ca. 100 Euro pro Reparatur\n- wenn das im Vertrag so steht und\n- ein Jahres-H√∂chstbetrag festgelegt wurde. \n- **Durchsetzung:**\n- Den Anspruch auf Beseitigung des Mangels kann man gegen√ºber dem\nVermieter geltend machen, indem man ihn auffordert, den Mangel zu beseitigen. Oft geschieht dies gemeinsam\nmit der Mitteilung √ºber den Mangel.":"- **claim**\n  - Landlords have a duty to keep the rented property in a state\nto hand over and receive that corresponds to the contractual agreement. \n  - That's what tenants pay the rent for. \n  - If a defect occurs during the rental period, then the landlord must rectify it. \n- **Exception**: for small repairs up to an amount of approx. 100 euros per repair\n  - if it says so in the contract and\n  - a maximum annual amount has been set. \n- **Enforcement:**\n  - The claim for elimination of the defect can be made against the\nClaim landlords by asking them to remedy the defect. This often happens together\nwith the notification of the defect.","- Ein Anspruch auf **Ausgleichszahlung** ist das Recht auf eine **Entsch√§digung** durch Ihre Fluglinie.\n- Sie bekommen eine Ausgleichszahlung, wenn Sie Ihr Ziel wegen **Versp√§tung**, **Annulierung** oder **√úberbuchung** nicht oder nur versp√§tet erreichen konnten.\n- Die **H√∂he der Zahlung** richtet sich nach der **Flugstrecke** und betr√§gt **250‚Ç¨ (unter 1500km)**, **400‚Ç¨ (bis zu 3500km)** oder **600‚Ç¨ (√ºber 3500km)**.":"- A claim to**Compensation** is the right to a**Compensation** from your airline.\n- You will receive a chargeback if you were unable to reach your destination or were delayed due to **delay**, **cancellation**, or **overbooking**.\n- The **amount of payment** depends on the**flight distance** and is ‚Ç¨250 (under 1500 km) **, **‚Ç¨400 (up to 3500 km) ** or**‚Ç¨600 (over 3500 km) **.","- Ein Anspruch auf **Ausgleichszahlung** ist das Recht auf eine **Entsch√§digung** durch Ihre Fluglinie.\n- Sie bekommen eine Ausgleichszahlung, wenn Sie Ihr Ziel wegen **Versp√§tung**, **Annulierung** oder **√úberbuchung** nicht oder nur versp√§tet erreichen konnten.\n- Die **H√∂he der Zahlung** richtet sich nach der **Flugstrecke** und betr√§gt **250‚Ç¨ (unter 1.500km)**, **400‚Ç¨ (zwischen 1.500km und 3.500km)** oder **600‚Ç¨ (√ºber 3.500km)**.":"- A claim to **Compensation** is the right to a **Compensation** from your airline.\n- You will receive a chargeback if you were unable to reach your destination or were delayed due to **delay**, **cancellation**, or **overbooking**.\n- The **amount of payment** depends on the **flight route** and is **‚Ç¨250 (under 1,500 km)**, **‚Ç¨400 (between 1,500 km and 3,500 km)** or **‚Ç¨600 (over 3,500 km)**.","- Ein Anspruch auf **Betreuungsleistungen** ist das Recht auf zus√§tzliche Unterst√ºtzung wie Essen und Getr√§nke, wenn sich Ihr Abflug stark verz√∂gert.\n- Betreuungsleistungen m√ºssen angemessen zur Wartezeit und Tageszeit sein. (Kleine) Mahlzeiten und Getr√§nke sowie Zugang zu  Telefon oder Internet sind immer angemessen.\n- Wenn **lange Wartezeiten am Flughafen bei Nacht** entstehen, kann auch das Recht auf eine **Hotelunterbringung** mit Transport dorthin bestehen.":"- A claim to **care services** is the right to additional support such as food and drink if your departure is severely delayed.\n- Care services must be appropriate to the waiting time and time of day. (Small) meals and drinks, and access to telephone or internet are always appropriate.\n- If there are **long waiting times at the airport at night**, you may also have the right to a **hotel accommodation** with transport there.","- Ein Anspruch auf **M√§ngelbeseitigung** ist das Recht, Ihre **Vermieter:in aufzufordern, den Mangel zu beseitigen**.\n- Vermieter:innen haben die Pflicht, die Mietwohnung in einem nutzbaren Zustand zu √ºbergeben und zu erhalten.\n- Wenn w√§hrend der Mietzeit ein Mangel auftritt, muss die Vermieter:in ihn beseitigen.":"- A claim to **remedial defects** is the right to ask your landlord to remedy the defect.\n- Landlords have the duty to hand over and maintain the rented apartment in a usable condition.\n- If a defect occurs during the rental period, the landlord must eliminate it.","- Ein Anspruch auf **M√§ngelbeseitigung** ist das Recht, Ihre **Vermieter:in aufzufordern, den Mangel zu beseitigen**.\n- Vermieter:innen haben die Pflicht, die Mietwohnung in einem nutzbaren Zustand zu √ºbergeben und zu erhalten.\n- Wenn w√§hrend der Mietzeit ein Mangel auftritt, muss die Vermieter:in ihn beseitigen.\n- Ausnahme: Mietende k√∂nnen f√ºr Kleinreperaturen bis etwa 100 Euro veranwortlich sein, wenn das im Mietvertrag festgelegt wurde.":"- A claim to**remedial defects** is the right to ask your **landlord to remedy the defect**.\n- Landlords have the duty to hand over and maintain the rented apartment in a usable condition.\n- If a defect occurs during the rental period, the landlord must eliminate it.\n- Exception: End of rental can be responsible for small repairs up to around 100 euros if this has been stipulated in the rental agreement.","- Ein Anspruch auf **M√§ngelbeseitigung** ist das Recht, Ihre **Vermieter:in aufzufordern, den Mangel zu beseitigen**.\n- Vermieter:innen haben die Pflicht, die Mietwohnung in einem nutzbaren Zustand zu √ºbergeben und zu erhalten.\n- Wenn w√§hrend der Mietzeit ein Mangel auftritt, muss die Vermieter:in ihn beseitigen.\n- Ausnahme: Mieter k√∂nnen f√ºr Kleinreperaturen bis etwa 100 Euro veranwortlich sein, wenn das im Mietvertrag mit einem Jahresh√∂chstbetrag festgelegt wurde.":"- A claim to**remedial defects** is the right to ask your **landlord to remedy the defect**.\n- Landlords have the duty to hand over and maintain the rented apartment in a usable condition.\n- If a defect occurs during the rental period, the landlord must eliminate it.\n- Exception: Tenants can be responsible for small repairs of up to around 100 euros if this has been specified in the rental agreement with a maximum annual amount.","- Ein Anspruch auf **R√ºckerstattung der Flugscheinkosten** ist das Recht, Ihren gezahlten Ticketpreis bei Annulierung innerhalb einer Woche zur√ºckzuerhalten.\n- Zus√§tzlich erhalten Sie einen R√ºckflug zu Ihrem ersten Abflugort, wenn ein Anschlussflug annuliert wurde.\n- Statt der R√ºckerstattung k√∂nnen Sie auch eine anderweitige Bef√∂rderung zu Ihrem Zielort durch die Fluglinie w√§hlen.":"- A claim to **Ticket Fee Refund** is the right to receive your paid ticket price back within one week if cancelled.\n- In addition, you will receive a return flight to your first departure location if a connecting flight has been cancelled.\n- You can choose to transfer the airline to your destination instead of reimbursement.","1. Anspr√ºche Pr√ºfen":"1. Check claims","2. M√∂gliche Anspr√ºche":"2. Show possible claims","3. N√§chste Schritte":"3. Next steps","Annullierung durch die Fluglinie":"Airline cancellation","Arbeit":"Work","Aufwendungsersatz":"Reimbursement of expenses","Aus Ihren Angaben k√∂nnten sich diese Anspr√ºche ergeben":"These claims could result from your details","Aus Ihren Angaben k√∂nnten sich folgende Anspr√ºche ergeben":"The following claims could result from your information","Ausgleichszahlung":"Chargeback","Au√üergerichtliche Einigung":"Out-of-court settlement","Bef√∂rderungs-verweigerung bei √úberbuchung":"Denial of carriage in case of overbooking","Bei Versp√§tungen der Landung unter 3 Stunden haben Sie leider keinen Anspruch auf Ausgleichszahlung. Es kann aber ein Anspruch auf Verpflegung bestehen, zum Beispiel Essen oder Getr√§nke.":"If landing delays are less than 3 hours, you are unfortunately not entitled to compensation. However, you may be entitled to food, such as food or drinks.","Bei einer Annullierung kann Ihnen die Fluglinie einen alternativen Flug anbieten. Dabei k√∂nnen Sie entscheiden, ob Sie den Flug annehmen oder die Flugscheinkosten zur√ºckerstattet haben wollen. Die Kosten k√∂nnen auch von einem Reiseveranstalter oder Reiseb√ºro erstattet werden.":"If you cancel, the airline can offer you an alternative flight. You can decide whether you want to accept the flight or have the ticket costs refunded. The costs can also be reimbursed by a tour operator or travel agency.","Bei einer Annullierung kann Ihnen die Fluglinie einen alternativen Flug anbieten. Dabei k√∂nnen Sie entscheiden, ob Sie den Flug annehmen oder die Flugscheinkosten zur√ºckerstattet haben wollen. Die Kosten k√∂nnen auch von einem Reiseveranstalter oder Reiseb√ºro erstattet werden.\nWenn Sie statt Ihrem annulierten Flug ein Angebot f√ºr einen Ersatzflug angenommen haben, k√∂nnen Ihnen noch Anspr√ºche auf Ausgleichszahlung entstehen.":"If you cancel, the airline can offer you an alternative flight. You can decide whether you want to accept the flight or have the ticket costs refunded. The costs can also be reimbursed by a tour operator or travel agency.\nIf you accepted an offer for a replacement flight instead of your cancelled flight, you may still be entitled to compensation.","Beratungsangebote":"Legal advice","Beseitigung des Mangels durch die Vermieter:in":"Removal of the defect by the landlords","Besteht der Schimmel aktuell noch?":"Does the mould still exist?","Betreuungsleistungen (Essen, Getr√§nke & Unterbringung)":"Care services (food, drinks & accommodation)","Da f√ºr das gesamte Projekt nur zw√∂lf Wochen zur Verf√ºgung standen, konnte dieser Pfad leider noch nicht implementiert werden.":"As only twelve weeks were available for the entire project, this path could not yet be implemented.","Damit Sie Anspr√ºche f√ºr Ihre Buchung geltend machen k√∂nnen, ben√∂tigen Sie eine Best√§tigung f√ºr Ihre Buchung. Das kann eine Best√§tigungs-E-Mail sein, eine Rechnung, oder auch Ihr Boarding Pass.":"To claim your booking, you need a confirmation of your booking. This could be a confirmation email, an invoice, or even your boarding pass.","Damit die EU-Fluggastrechteverordnung gilt, muss einer der folgenden F√§lle vorliegen:\n- Der Startflughafen liegt in Europa oder der Schweiz\n- Der Zielflughafen liegt in Europa UND das Luftfahrtunternehmen hat den Sitz in der EU oder der Schweiz":"In order for the EU passenger rights regulation to apply, one of the following cases must exist:\n- The departure airport is in Europe or Switzerland\n- The destination airport is in Europe AND the airline is based in the EU or Switzerland","Die Justiz im Netz.":"Justice online.","Die Verordnung gilt nicht, wenn Sie kostenlos oder zu einem nicht √∂ffentlichen Tarif reisen (z.B. mit Mitarbeitertickets). \nEin durch ein Werbeprogramm bezahlter Flug (z.B. Bonusmeilen) ist aber abgedeckt.":"The regulation does not apply if you travel free of charge or at a non-public rate (e.g. with employee tickets). \nHowever, a flight paid for through an advertising program (such as bonus miles) is covered.","Diese Frage hilft uns, Ihnen die richtigen n√§chsten Schritte zu empfehlen. Wenn der Mangel noch besteht, erkl√§ren wir Ihnen beispielsweise, wie Sie diesen beseitigen lassen k√∂nnen.":"This question helps us recommend the right next steps for you. If the defect still exists, we will explain how you can have it rectified, for example.","Diese Option ist leider noch nicht verf√ºgbar.":"Unfortunately, this option is not yet available.","Dieser Pfad wurde noch nicht implementiert":"This path has not yet been implemented","Dokumentation des Mangels":"Documentation of the defect","Ein **Anspruch auf Ausgleichszahlung** kann bei einer kurzfristigen Annullierung nur bestehen, wenn die Fluglinie Ihnen **keine vergleichbare Bef√∂rderung** angeboten hat. \nAls vergleichbar gilt:\n- Bei Annulierung zwischen 7 und 14 Tagen vor Abflug: Neuer Abflug nicht mehr als zwei Stunden fr√ºher, und neue Landung h√∂chstens vier Stunden sp√§ter als gebucht.\n- Bei Annulierung innerhalb von 7 Tagen vor Abflug: Neuer Abflug nicht mehr als eine Stunde fr√ºher, und neue Landung h√∂chstens zwei Stunden sp√§ter als gebucht.":"**Chargeback can only be eligible for short-term cancellation** if the airline has offered you **no comparable transportation**. \nThe following is considered comparable:\n- If cancelled between 7 and 14 days before departure: new departure no more than two hours earlier, and new landing no more than four hours later than booked.\n- If cancelled within 7 days of departure: new departure no more than an hour earlier, and new landing no more than two hours later than booked.","Ein Anspruch auf Ausgleichszahlung entsteht erst ab drei Stunden Versp√§tung am Zielflughafen. Versp√§tungen beim Start sind dabei irrelevant. \nAls Landezeit gilt der Moment, in dem die Flugzeugt√ºren ge√∂ffnet werden.":"A claim to compensation only arises from a delay of three hours at the destination airport. Delays at launch are irrelevant. \nLanding time is considered to be the moment when the aircraft doors are opened.","Ein Anspruch ist das Recht, ein Verhalten von einer anderen Person zu fordern. Klicken Sie auf die Anspr√ºche, um mehr dar√ºber zu erfahren.":"A claim is the right to demand behavior from another person. Click on the claims to learn more about them.","Ein Anspruch ist das Recht, etwas von einer anderen Person oder einem Unternehmen zu fordern. Klicken Sie auf die Anspr√ºche, um mehr dar√ºber zu erfahren.":"A claim is the right to demand something from another person or company. Click on the claims to learn more about them.","Ein Anspruch ist das Recht, etwas von einer anderen Person oder einem Unternehmen zu fordern. Sie k√∂nnen mehrere Anspr√ºche durchsetzen. \nKlicken Sie auf die Anspr√ºche, um mehr dar√ºber zu erfahren. Auf der n√§chsten Seite informieren wir Sie dann √ºber m√∂gliche n√§chste Schritte.":"A claim is the right to demand something from another person or company. You can enforce multiple claims. \nClick on the claims to learn more about them. We will then inform you about possible next steps on the next page.","Ein Zahlungsbeweis ist notwendig, um einen Anspruch auf R√ºckerstattung der Flugscheinkosten geltend zu machen. Suchen Sie noch einmal - vielleicht k√∂nnen Sie einen Zahlungsbeweis in Ihren E-Mails finden, oder bei Ihrem Reiseb√ºro oder Ihrer Bank anfragen.\nVermutlich stehen Ihnen dar√ºber hinaus keine weiteren Anspr√ºche mehr zu, da Ihr Flug nicht kurzfristig annuliert wurde.":"Proof of payment is required to claim a refund of ticket costs. Search again ‚Äî you might be able to find proof of payment in your emails, or ask your travel agent or bank.\nYou probably won't be entitled to any further claims as your flight was not cancelled at short notice.","Ein anderes Problem":"Another problem","Eine Buchungsbest√§tigung ist notwendig, um Anspr√ºche aus Ihrem Flug geltend zu machen. Suchen Sie noch einmal - vielleicht k√∂nnen Sie eine Best√§tigung in Ihren E-Mails finden, oder bei Ihrem Reiseb√ºro anfragen.":"A booking confirmation is required to file claims from your flight. Search again - you might be able to find a confirmation in your emails or ask your travel agent.","Einfach, online, jederzeit.":"Simple, anywhere, at any time.","Einkaufen":"Shopping","Erstberatung":"Initial consultation","Familie":"Family","Fand der Flug innerhalb der letzten drei Jahre statt?":"Did the flight take place within the last three years?","Fluggastrechte":"Passenger rights","Fluglinie kontaktieren":"Contact airline","F√ºr einen Anspruch auf Ausgleichszahlung ist es entscheidend, dass Ihnen die Bef√∂rderung verweigert wurde, obwohl Sie rechtzeitig beim Boarding waren. Wenn kein anderer Termin von Ihrer Fluglinie angegeben wurde, reichen 5 Minuten vor Abflug.":"It is critical that you were denied boarding even though you were on time for a chargeback claim. If no other appointment has been specified by your airline, 5 minutes before departure is enough.","F√ºr einen Anspruch auf Ausgleichszahlung ist es entscheidend, dass Ihnen die Bef√∂rderung verweigert wurde, obwohl Sie rechtzeitig beim Boarding waren. Wenn kein anderer Termin von Ihrer Fluglinie angegeben wurde, reichen 5 Minuten vor Abflug.\\n":"It is critical that you were denied boarding even though you were on time for a chargeback claim. If no other appointment has been specified by your airline, 5 minutes before departure is enough.","F√ºr einen Anspruch auf Ausgleichszahlung ist es entscheidend, dass Sie sich rechtzeitig zum Check-in eingefunden haben. Wenn kein anderer Termin von Ihrer Fluglinie angegeben wurde, reichen 45 Minuten vor Abflug.":"To claim a chargeback, it is crucial that you arrive in time for check-in. If no other appointment has been specified by your airline, it is enough 45 minutes before departure.","Gab es den Schimmel schon bei Unterzeichnung des Mietvertrags?":"Did the mold exist when signing the rental agreement?","Gerichtsverfahren":"Trials","Geschafft! Aus Ihren Angaben k√∂nnten sich folgende Anspr√ºche ergeben":"Done! The following claims could result from your information","Geschafft! Basierend auf den von Ihnen angegebenen Informationen, k√∂nnten die folgenden rechtlichen Anspr√ºche\nf√ºr Sie in Frage kommen. Ein Anspruch ist das Recht ein bestimmtes Verhalten von einer anderen Person\neinzufordern.":"Done! Based on the information you provide, the following legal claims could\ncome into question for you. A claim is the right to a particular behavior by another person\nto demand.","Haben Sie Ihre Fluglinie schon aufgefordert, die Flugscheinkosten zu erstatten?":"Have you asked your airline to refund ticket costs yet?","Haben Sie Ihre Fluglinie schon aufgefordert, eine Ausgleichszahlung an Sie zu zahlen und daf√ºr eine realistische Frist gesetzt?":"Have you asked your airline to pay you a chargeback and set a realistic deadline for doing so?","Haben Sie Ihre Fluglinie schon aufgefordert, eine Zahlung an Sie zu leisten und daf√ºr eine realistische Frist gesetzt?":"Have you asked your airline to make a payment to you and set a realistic deadline for doing so?","Haben Sie Ihrer Vermieter:in eine realistische Frist gesetzt, um den Schimmel zu entfernen?":"Have you set your landlord in a realistic deadline to remove the mold?","Haben Sie den Schimmel Ihrer Vermieter:in schriftlich gemeldet?":"Have you reported your landlord's mould in writing?","Haben Sie den Schimmel bei Ihrer Vermieter:in angezeigt und angek√ºndigt, Ihre Miete nur unter Vorbehalt weiter zu zahlen (Mietminderung)?":"Have you displayed the mould to your landlord:in and announced that you would only continue to pay your rent under reservation (rent reduction)?","Haben Sie den Schimmel selbst beseitigt, und sind Ihnen dabei Kosten entstanden?":"Did you remove the mould yourself and have you incurred costs in the process?","Haben Sie den Schimmel verursacht?":"Did you cause the mold?","Haben Sie ein Angebot f√ºr einen Ersatzflug angenommen, oder wurden ihnen die Flugscheinkosten schon vollst√§ndig erstattet?":"Have you accepted an offer for a replacement flight, or have you already received a full refund of ticket costs?","Haben Sie ein Ticket gekauft und selbst oder mit Bonusmeilen bezahlt?":"Did you buy a ticket and pay for it yourself or with bonus miles?","Haben Sie eine Best√§tigung f√ºr Ihre Buchung?":"Do you have a confirmation for your booking?","Haben Sie einen Zahlungsbeweis f√ºr Ihren Flugschein?":"Do you have proof of payment for your ticket?","Handlungsoptionen vergleichen":"Compare options for action","Hat Ihre Fluglinie au√üergew√∂hnliche Umst√§nde f√ºr die Annullierung genannt, die Sie nicht vermeiden konnte?":"Did your airline mention exceptional circumstances for the cancellation that you couldn't avoid?","Hat Ihre Fluglinie au√üergew√∂hnliche Umst√§nde f√ºr die Versp√§tung genannt, die Sie nicht vermeiden konnte?":"Did your airline mention exceptional circumstances for the delay that you couldn't avoid?","Heizungsausfall":"Heater failure","Ihr Flug wurde kurzfristig storniert, aber Ihnen wurde ein alternativer vergleichbarer Flug  angeboten. Da Sie diesen angenommen haben, oder Ihnen bereits die Kosten des Flugscheins zur√ºckerstattet wurden, liegen vermutlich keine weiteren Anspr√ºche vor.":"Your flight was cancelled at short notice, but you were offered an alternative comparable flight. Since you accepted this, or you have already been reimbursed the cost of the ticket, you probably have no further claims.","In welchem Bereich Ihres Lebens haben Sie ein Problem?":"What area of your life do you have a problem in?","Info":"Info","Ist die Frist zur M√§ngelbeseitigung schon abgelaufen?":"Has the deadline for rectification of defects already expired?","Ist ihr Flug in der EU/Schweiz gestartet, oder sind Sie mit einer EU-Fluglinie in der EU/Schweiz gelandet?":"Did your flight depart from the EU/Switzerland or did you land in the EU/Switzerland on an EU airline?","Ja":"Yes","Jetzt starten!":"Start now!","K√ºndigung":"Termination","Leider f√§llt Ihr Flug nicht in den Anwendungsbereich der EU-Fluggastrechteverordnung, da Sie kein regul√§res Ticket gekauft haben. Deshalb k√∂nnen Sie √ºber das Portal keine Anspr√ºche geltend machen.":"Unfortunately, your flight does not fall within the scope of the EU passenger rights regulation because you did not buy a regular ticket. Therefore, you cannot make any claims via the portal.","Leider f√§llt Ihr Flug nicht in den Anwendungsbereich der EU-Fluggastrechteverordnung. Deshalb k√∂nnen Sie √ºber das Portal keine Anspr√ºche geltend machen.":"Unfortunately, your flight does not fall within the scope of the EU passenger rights regulation. Therefore, you cannot make any claims via the portal.","L√§rm":"Noise","L√∂sungsfinder":"Solutionfinder","Mieterh√∂hung":"Rent increase","Mietminderung":"Rent reduction","Mietpreisbremse":"Rent brake\n(Mietpreisbremse)","M√§ngel in der Wohnung":"Deficiencies in the apartment","M√§ngelanzeige bei Ihrer Vermieter:in":"Notice letter of defects to your landlord","M√∂gliche Anspr√ºche":"Possible claims","Nebenkosten-abrechnung":"Utilities billing","Nein":"No","Nur kostenlose Angebote anzeigen":"Show free offers only","N√§chste Schritte":"Next steps","Online Klage einreichen":"File a lawsuit online","Problem einordnen":"Classify issue","Rechtlich beraten lassen":"Get legal advice","Rechtsproblem einordnen":"Classify legal problem","Reisen & Verkehr":"Travel & traffic","Renovierung bei Auszug":"Renovation when moving out","R√ºckerstattung der Flugscheinkosten":"Ticket reimbursement","Schadensersatz":"Damages","Schimmel":"Mould","Schimmel in der Wohnung kann durch Baufehler entstehen, f√ºr die Sie nicht  verantwortlich sind. Allerdings kann auch seltenes L√ºften und Heizen oder das Trocknen von W√§sche zu Schimmel f√ºhren.\nWenn Sie durch Ihr Verhalten den Schimmel ganz oder teilweise mitverursacht haben, bestehen in der Regel keine Anspr√ºche gegen ihre Vermieter:in.":"Mold in the apartment can be caused by construction failures, for which you are not responsible. However, infrequent airing and heating or drying laundry can also cause mould.\nIf you have partly or completely caused the mould as a result of your behaviour, there are usually no claims against your landlords:in.","Schlichtungsantrag stellen":"Submit a request for conciliation","Sie haben Ihren Mangel angezeigt und eine Frist zur Beseitigung gesetzt? Dann m√ºssen Sie zun√§chst abwarten, bis diese abgelaufen ist. Falls der Mangel danach noch nicht beseitigt wurde, k√∂nnen Sie weitere Schritte unternehmen, und zum Beispiel den Mangel selbst beseitigen.":"Have you reported your defect and set a deadline for remediation? Then you have to wait for it to expire first. If the defect has not been rectified afterwards, you can take further steps, such as correcting the defect yourself.","Sie haben angegeben, dass die Kosten f√ºr Ihren Flug noch nicht erstattet wurden. Um einen Anspruch auf R√ºckerstattung durchsetzen zu k√∂nnen, m√ºssen Sie einen Zahlungsbeleg vorweisen k√∂nnen. Dazu z√§hlen beispielsweise:\n- Zahlungsbest√§tigung der Fluglinie\n- Kontoauszug mit der Zahlung\n- Kreditkartenabrechnung mit der Zahlung":"You've indicated that the cost of your flight has not yet been refunded. In order to enforce a claim for a refund, you must be able to show a proof of payment. This includes, for example:\n- Airline payment confirmation\n- bank statement with payment\n- credit card statement with payment","Sie haben nur eine realistische Aussicht auf eine Ausgleichszahlung, wenn Ihre Fluglinie die Annullierung nicht vermeiden konnte. \nBeispiele f√ºr au√üergew√∂hnliche Umst√§nde sind:\n- politische Instabilit√§t\n- starkes Unwetter\n- Sicherheitsrisiken\n- Unerwartete M√§ngel am Flugzeug\n- Streik am Flughafen\n- Entscheidungen des Flugverkehrsmanagements":"You only have a realistic chance of getting a chargeback if your airline couldn't avoid the cancellation. \nExamples of exceptional circumstances include:\n- political instability\n- strong storm\n- security risks\n- Unexpected defects on aircraft\n- strike at the airport\n- Air traffic management decisions","Sie haben nur eine realistische Aussicht auf eine Ausgleichszahlung, wenn Ihre Fluglinie die Versp√§tung nicht vermeiden konnte. \nBeispiele f√ºr au√üergew√∂hnliche Umst√§nde sind:\n- politische Instabilit√§t\n- starkes Unwetter\n- Sicherheitsrisiken\n- Unerwartete M√§ngel am Flugzeug\n- Streik am Flughafen\n- Entscheidungen des Flugverkehrsmanagements":"You only have a realistic chance of getting a chargeback if your airline couldn't avoid the delay. \nExamples of exceptional circumstances include:\n- political instability\n- strong storm\n- security risks\n- Unexpected defects on aircraft\n- strike at the airport\n- Air traffic management decisions","Sie k√∂nnen Ihre Anspr√ºche nur durchsetzen, wenn der Flug innerhalb der letzten drei Jahre stattfand. Der genaue Tag und Monat spielen dabei keine Rolle.\nBeispiel: Im Jahr 2021 sind alle Fl√ºge ab dem 1.1.2018 von der Verordnung abgedeckt.":"You can only enforce your claims if the flight took place within the last three years. The exact day and month don't matter.\nExample: In 2021, all flights from 1 January 2018 will be covered by the regulation.","Solution Explorer":"Solution explorer","Stromausfall":"Blackout","Unser Angebot":"Our offer","Verkehrsunfall":"Traffic accident","Vermieter:in erneut kontaktieren":"Contact Landlord in again","Vermutlich haben Sie einen Anspruch auf Erstattung der Flugscheinkosten. Die Kosten sollten innerhalb von sieben Tagen nach Annullierung erstattet werden. Ist das noch nicht geschehen, sollten Sie Ihre Fluglinie zun√§chst selbst zur Zahlung auffordern. Dabei k√∂nnen wir Ihnen helfen.":"You are probably entitled to a refund of ticket costs. The costs should be reimbursed within seven days of cancellation. If that hasn't happened yet, you should first ask your airline to pay yourself. We can help you with that.","Versp√§tung":"Tardiness","War ihre Landung um mindestens drei Stunden versp√§tet?":"Was her landing delayed by at least three hours?","Waren Sie rechtzeitig am Check-in (mindestens 45 Minuten vor dem Start)?":"Were you at check-in in time (at least 45 minutes before the start)?","Waren Sie rechtzeitig beim Boarding (mindestens 5 Minuten vor dem Start)?":"Were you on time boarding (at least 5 minutes before the start)?","Wasserschaden":"Water damage","Weiter":"Continue","Wenn Ihnen durch den Mangel keine Kosten oder Sch√§den entstanden sind, Sie keine Mietminderung angek√ºndigt haben und der Mangel bereits behoben wurde, haben Sie vermutlich keine weiteren rechtlichen Anspr√ºche.":"If you did not incur any costs or damage due to the defect, you have not announced a reduction in rent, and the defect has already been resolved, you probably have no further legal claims.","Wenn Ihr Flug kurzfristig annulliert wurde, k√∂nnen Ihnen zus√§tzliche Anspr√ºche entstehen, beispielsweise auf Ausgleichszahlung.":"If your flight was cancelled at short notice, you may incur additional entitlements, such as a chargeback payment.","Wenn Ihr Flug vor √ºber drei Jahren stattgefunden hat, ist Ihr Anspruch vermutlich verj√§hrt. Daher k√∂nnen wir Ihnen nicht weiterhelfen.":"If your flight took place over three years ago, your claim is probably time-barred. As a result, we can't help you any further.","Wenn Ihre Vermieter:in auch nach Aufforderung und Ablauf einer realistischen Frist den Mangel nicht beseitigt, oder es sich um eine Not- oder Eilma√ünahme handelt, k√∂nnen Sie den Mangel auch selbst beseitigen und sich die Kosten daf√ºr erstatten lassen.":"If your landlord does not remedy the defect, even after being requested and after a realistic deadline, or if it is an emergency or urgent measure, you can also rectify the defect yourself and have the costs reimbursed for it.","Wenn Ihre  Fluglinie au√üergew√∂hnliche Umst√§nde f√ºr Ihre Versp√§tung nennt, ist es schwer abzusch√§tzen, ob ein Anspruch auf Ausgleichszahlung vorliegt. Nicht immer sind die genannten Gr√ºnde gerechtfertigt. Um das zu pr√ºfen, k√∂nnen Sie sich zum Beispiel [von einer Expert:in beraten lassen](/beratungsangebote).\nWenn Sie Ihren Fall von einer Richter:in entscheiden lassen m√∂chten, k√∂nnen Sie auch klagen. [Hier informieren wir Sie, wie Sie eine Klage einreichen k√∂nnen](/zivil-prozess-info).":"If your airline lists exceptional circumstances for your delay, it's hard to tell if you're entitled to a chargeback. The reasons mentioned are not always justified. To check this, you can, for example, [seek advice from an expert](/beratungsangebote).\nIf you want a judge to decide your case, you can also sue. [You can find out how to file a claim here](/zivil-prozess-info).","Wenn Ihre  Fluglinie au√üergew√∂hnliche Umst√§nde f√ºr die Annullierung nennt, ist es schwer abzusch√§tzen, ob ein Anspruch auf Ausgleichszahlung vorliegt. Nicht immer sind die genannten Gr√ºnde gerechtfertigt. Um das zu pr√ºfen, k√∂nnen Sie sich zum Beispiel [von einer Expert:in beraten lassen](/beratungsangebote).\nWenn Sie Ihren Fall von einer Richter:in entscheiden lassen m√∂chten, k√∂nnen Sie auch klagen. [Hier informieren wir Sie, wie Sie eine Klage einreichen k√∂nnen](/zivil-prozess-info).":"If your airline specifies exceptional circumstances for the cancellation, it's hard to tell if you're entitled to a chargeback. The reasons mentioned are not always justified. To check this, you can, for example, [seek advice from an expert](/beratungsangebote).\nIf you want a judge to decide your case, you can also sue. [You can find out how to file a claim here](/zivil-prozess-info).","Wenn Sie Ihrer Vermieter:in eine realistische Frist gesetzt haben, m√ºssen Sie ihr zun√§chst bis dahin Zeit f√ºr die Beseitigung geben. Danach haben Sie zus√§tzliche rechtliche Anspr√ºche.":"If you have set a realistic deadline for your landlord, you must first give her time for removal by then. After that, you have additional legal rights.","Wenn Sie angek√ºndigt haben, Ihre Miete nur unter Vorbehalt zu zahlen, kann Ihnen von der Ank√ºndigung bis zur Beseitigung des Mangels sein Anspruch auf Mietminderung zustehen.":"If you have announced that you are only paying your rent on a conditional basis, you may be entitled to a reduction in rent from the announcement until the defect is rectified.","Wenn Sie den Mangel schon gemeldet haben, ist entscheidend, ob Sie dabei eine realistische Frist zur Beseitigung gesetzt haben. In der Regel sollten 2-3 Wochen ausreichen, um den Mangel zu beheben oder Ihnen zumindest einen Termin f√ºr eine Reperatur zu nennen. \nWenn Sie keine oder eine zu kurze Frist gesetzt haben, helfen wir Ihnen, ein neues Schreiben aufzusetzen.":"If you have already reported the defect, it is crucial whether you have set a realistic deadline for remedying it. Usually 2-3 weeks should be enough to fix the defect or at least give you an appointment for a repair. \nIf you have not set a deadline, or if you have set a deadline too short, we can help you rewrite a letter.","Wenn Sie den Mangel selbst verursacht haben, k√∂nnen vermutlich keine rechtlichen Anspr√ºche durchgesetzt werden. Eine Ausnahme ist, wenn Versicherungen f√ºr den Eintritt eines Mangels bestehen. Zahlen Sie √ºber Ihre Betriebskosten zum Beispiel eine Geb√§udeversicherung mit, dann muss diese bei Sch√§den zahlen. \nIn jedem Fall sollten Sie Ihre Vermieter:in kontaktieren, damit der Mangel beseitigt werden kann. [Nutzen Sie daf√ºr die Vorlage unserer M√§ngelanzeige](/formular?mangelanzeige&mangelart=schimmel).":"If you caused the defect yourself, it is likely that no legal claims can be enforced. An exception is if there is insurance for the occurrence of a defect. If you, for example, pay for building insurance via your operating costs, then you have to pay for damage. \nIn any case, you should contact your landlord so that the defect can be rectified. [Use the submission of our defect report](/formular?mangelanzeige&mangelart=schimmel).","Wenn Sie einen Mangel schon bei Unterschrift des Mietvertrags kannten, aber dabei nicht um Verbesserung gebeten haben, k√∂nnen Anspr√ºche ausgeschlossen sein. \nAnders ist die Situation, wenn der Mangel erst sp√§ter entstanden ist, oder bei Vetragsabschluss verschwiegen oder sogar versteckt wurde.":"If you knew about a defect when you signed the lease but did not ask for improvement, claims may be excluded. \nThe situation is different if the defect arose later, or was concealed or even hidden when the contract was concluded.","Wenn Sie einen Zahlungsbeweis f√ºr Ihren Flug haben, k√∂nnen Sie vermutlich einen **Anspruch auf R√ºckerstattung der Flugscheinkosten** durchsetzen.\nZus√§tzlich kann ein **Anspruch auf Ausgleichszahlung** wegen kurzfristiger Annullierung bestehen. Daf√ºr muss die Fluglinie Ihnen **keine vergleichbare Bef√∂rderung** angeboten haben. \nAls vergleichbar gilt:\n- Bei Annulierung zwischen 7 und 14 Tagen vor Abflug: Neuer Abflug nicht mehr als zwei Stunden fr√ºher, und neue Landung h√∂chstens vier Stunden sp√§ter als gebucht.\n- Bei Annulierung innerhalb von 7 Tagen vor Abflug: Neuer Abflug nicht mehr als eine Stunde fr√ºher, und neue Landung h√∂chstens zwei Stunden sp√§ter als gebucht.":"If you have proof of payment for your flight, you can probably enforce a **claim a ticket refund**.\nIn addition, a **entitlement to chargeback payment** may exist due to short-term cancellation. For this, the airline must have offered you **no comparable transportation**. \nThe following is considered comparable:\n- If cancelled between 7 and 14 days before departure: new departure no more than two hours earlier, and new landing no more than four hours later than booked.\n- If cancelled within 7 days of departure: new departure no more than an hour earlier, and new landing no more than two hours later than booked.","Wenn Sie nicht rechtzeitig beim Boarding waren, haben Sie vermutlich keinen Anspruch auf Ausgleichszahlung.":"If you didn't board on time, you probably won't be eligible for a chargeback.","Wenn Sie nicht rechtzeitig beim Check-in waren, haben Sie vermutlich keinen Anspruch auf eine Ausgleichszahlung.":"If you didn't arrive at check-in on time, you probably won't be eligible for a chargeback.","Wenn Sie sich √ºber Ihre Anspr√ºche informiert haben, zeigen wir Ihnen, \nwie Sie diese durchsetzen k√∂nnen. Klicken Sie daf√ºr einfach auf Weiter.":"Once you've been informed about your claims, we'll show you \nhow you can enforce them. Just click Next to do so.","Wenn Sie statt Ihrem annulierten Flug ein Angebot f√ºr einen Ersatzflug angenommen haben, oder die Flugscheinkosten schon erstattet wurden, haben Sie vermutlich keine weiteren Anspr√ºche, da Ihr Flug nicht kurzfristig annulliert wurde.":"If you accepted an offer for a replacement flight instead of your cancelled flight, or the ticket costs have already been reimbursed, you probably have no further entitlement as your flight wasn't cancelled at short notice.","Wenn bei einer kurzfristigen Annullierung kein vergleichbarer Flug angeboten wurde, steht Ihnen vermutlich eine Ausgleichszahlung zu.\nZun√§chst sollten Sie Ihre Fluglinie selbst auffordern, eine Ausgleichszahlung an Sie zu leisten. Wenn Sie das noch nicht getan haben, helfen wir Ihnen dabei.":"If a comparable flight was not offered for a short-term cancellation, you may be entitled to a chargeback.\nFirst, you should ask your airline itself to make a chargeback payment to you. If you haven't done that yet, we'll help you do it.","Wenn bei einer kurzfristigen Annullierung kein vergleichbarer Flug angeboten wurde, steht Ihnen vermutlich eine Ausgleichszahlung zu. Eine Ausnahme sind au√üergew√∂hnliche Umst√§nde, bei denen die Fluglinie die Annullierung nicht vermeiden konnte.\nZus√§tzlich haben Sie vermutlich einen Anspruch auf Erstattung der Flugscheinkosten. Die Kosten sollten innerhalb von sieben Tagen nach Annullierung erstattet werden.\nZun√§chst sollten Sie Ihre Fluglinie selbst auffordern, die Zahlungen an Sie zu leisten. Wenn Sie das noch nicht getan haben, helfen wir Ihnen dabei.":"If a comparable flight was not offered for a short-term cancellation, you may be entitled to a chargeback. Exceptional circumstances are exceptional circumstances where the airline could not avoid the cancellation.\nIn addition, you may be entitled to a refund of ticket costs. The costs should be reimbursed within seven days of cancellation.\nFirst, you should ask your airline to make the payments to you. If you haven't done that yet, we'll help you do it.","Wenn der Mangel noch besteht, sollten Sie diesen zuerst Ihrer Vermieter:in melden, um ihn beseitigen zu lassen. Sie sollten dabei eine schriftliche M√§ngelanzeige verschicken, um einen Nachweis zu haben.\nWenn Sie das noch nicht getan haben, helfen wir Ihnen dabei.":"If the defect still persists, you should report it to your landlord first to have it rectified. You should send a written notice of defects in order to have proof.\nIf you haven't done that yet, we'll help you do it.","Wenn der Mangel schon bei Vertragsschluss bekannt war, k√∂nnen vermutlich keine rechtlichen Anspr√ºche durchgesetzt werden. Sie k√∂nnen aber den direkten Austausch mit Ihrer Vermieter:in suchen.":"If the defect was already known when the contract was concluded, no legal claims can probably be enforced. However, you can seek direct exchange with your landlord.","Wenn durch den Mangel nicht nur an der Wohnung, sondern auch an Ihrem Eigentum Sch√§den entstanden sind, haben sie vielleicht Anspr√ºche auf Schadensersatz.\nZus√§tzliche Sch√§den k√∂nnen sein, wenn durch den Mangel zum Beispiel eigene M√∂bel oder Kleidung besch√§digt wurden.":"If the defect caused damage not only to the apartment, but also to your property, you may be entitled to compensation.\nAdditional damage can occur if the defect has damaged one's own furniture or clothing, for example.","Wie es funktioniert":"How it works","Wobei ben√∂tigen Sie Unterst√ºtzung?":"What do you need support with?","Wobei k√∂nnen wir Ihnen helfen?":"What can we help you with?","Wohnen":"Dwelling","Wurde Ihnen ein alternativer Flug angeboten, mit dem Sie Ihr Ziel nur wenige Stunden sp√§ter erreichen konnten?":"Have you been offered an alternative flight that allowed you to reach your destination just a few hours later?","Wurde Ihr Flug weniger als zwei Wochen vor dem Abflug annulliert?":"Was your flight cancelled less than two weeks before departure?","Wurde durch den Schimmel Eigentum von Ihnen besch√§digt?":"Has the mold damaged your property?","Zu den Beratungsangeboten":"Go to advisory services","Zum Prototypen":"Go to the prototype","Zun√§chst sollten Sie Ihre Fluglinie selbst auffordern, eine Ausgleichszahlung an Sie zu leisten. Wenn Sie das noch nicht getan haben, helfen wir Ihnen dabei.":"First, you should ask your airline itself to make a chargeback payment to you. If you haven't done that yet, we'll help you do it.","Zur√ºck":"Back","Zur√ºck zur Homepage":"Back to homepage","Zweites Schreiben an die Vermieter:in senden":"Send a second letter to landlord","back":"Back","exit.heading":"Unfortunately, we didn't cover her case","footer.disclaimer":"This page is a prototype with no guarantee of legal certainty!","footer.info":"You can find more information about this page here","footer.text":"Built by ‚öñÔ∏è and üêå with ‚ù§Ô∏è","forward":"Continue","home.hero_offer":"Justizportal.de is an offer of the Federal Ministry of Justice and Consumer Protection in cooperation with the federal ministries of justice in the federal states. The portal gives you an overview of your rights in various everyday situations. It also helps you to classify and solve your legal questions and problems. You will be informed about your options for action and will be supported in asserting your rights.","home.howitwork_claims":"We will then help you to evaluate which claims might apply in your case. Claims are rights to demand a certain action from others (e.g. payment of damages), to tolerate (e.g. subletting a flat) or to refrain from (e.g. entering a property).","home.howitwork_classify":"First, we'll help you clarify whether there is a legal problem in your case. We will show you information tailored to your case.","home.howitwork_heading":"How it works","home.howitwork_lawsuit":"When all other options are exhausted, we'll help you file a claim with the district court online. To this end, we explain the process of our online process to you and provide transparent information on the costs of legal proceedings as well as possible legal advice.","home.howitwork_options":"Based on this, we will suggest possible solutions for <0>you - initially for an amicable dispute resolution with the opposite side.</0>","home.howitwork_subheading":"We can help you solve your legal problem in four easy steps:","home.howtohelp_footer":"You can find out more about the legal problems we can help you with on the following pages.","home.sub_heading":"We inform you about your rights in everyday life and help you solve your legal problem in simple steps","nextsteps.header":"Your next possible steps to solve your problem","nextsteps.sub_header":"We have summarized the possible steps for you to enforce your claims here. Click on the boxes one by one to learn more and get support.","pe.to_next_steps":"To the next steps","√úber Anspr√ºche informieren":"Inform about claims","√úber das Justizportal eine Klage einreichen":"File a claim via the Justizportal"}};